.home {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  padding: 0 20px;
}

.header {
  padding-top: 10px;
}

.menuBtn {
  align-items: center;
  background: none;
  border: none;
  border-radius: 3px;
  fill: #fff;
  cursor: pointer;
  display: flex;
  height: 36px;
  justify-content: center;
  outline: none;
  width: 36px;
}

.menuBtn:hover {
  background: #4a5a99;
}

.menuBtn svg {
  height: 22px;
  width: 22px;
}

.menu {
  color: var(--color-text);
  font-size: 14px;
  padding: 0 10px;
}

.menuHeader {
  padding-top: 10px;
}

.menuHeader .menuBtn {
  fill: #000;
}

.menuHeader .menuBtn:hover {
  background: #f4f4f4;
}

.menuHeader svg {
  height: 22px;
  width: 22px;
}

.menuContent {
  grid-gap: 5px;
  display: grid;
  gap: 5px;
  margin-top: 15px;
}

.menuContent a {
  align-items: center;
  border-radius: 3px;
  color: var(--color-text);
  display: flex;
  padding: 10px;
  text-decoration: none;
}

.menuContent .active {
  background: #e8e8e8;
  color: var(--color-primary);
}

.menuItem:hover {
  background: #e8e8e8;
}

.menuItem svg {
  height: 18px;
  margin-right: 10px;
  width: 18px;
}

.active svg {
  fill: var(--color-primary);
}

.main {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  overflow: auto;
}


