.taskInput {
  align-items: center;
  background: #eeeff3;
  border-radius: 3px;
  color: var(--color-text);
  display: flex;
  margin-bottom: 15px;
  margin-top: 5px;
  padding: 12px 10px;
}
.taskInput svg {
  height: 20px;
  width: 20px;
}

input {
  background: none;
  border: none;
  color: var(--color-text);
  flex: 1 1;
  font-size: 14px;
  margin-left: 10px;
  outline: none;
  overflow: hidden;
  text-overflow: ellipsis;
}