.taskItem {
  align-items: center;
  background: #eeeff3;
  border-radius: 3px;
  color: var(--color-text);
  display: flex;
  margin-bottom: 5px;
  padding: 15px 10px;
}

.taskItem:hover {
  background: #fff;
}

.circleWrap,.starWrap {
  display: flex;
}

.circleWrap svg {
  height: 20px;
  width: 20px;
}

.starWrap svg {
  height: 20px;
  width: 20px;
}

.checked {
  fill: var(--color-primary);
}

.stared {
  fill: var(--color-primary);
}

.content {
  flex: 1 1;
  padding: 0 15px;
  word-break: break-all;
}

.finishedText {
  color: #919191;
  text-decoration: line-through;
}